@import "@/assets/global.scss";

























































































































.head {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.head > img {
  position: absolute;
  top: 50%;
  right: 22px;
  transform: translate(0, -50%);
  cursor: pointer;
}

.content {
  height: calc(100vh - 78px - 60px);
  overflow-y: auto;
  padding: 0 16px;
  box-sizing: border-box;
}

.midContent {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 32px 0;

  > .midItem {
    display: flex;
    align-items: center;
    flex-direction: column;
    > div {
      background: #F2F2F2;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}
