@import "@/assets/global.scss";
































.head {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.listContent {
  height: 172px;
  width: 100%;
  overflow-x: auto;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  > div {
    display: flex;
    margin: 8px 36px 8px 0;
  }
}

.txtContent {
  width: 140px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 8px;

  > span {
    text-align: left;
  }
}
