@import "@/assets/global.scss";





















































.carousel {
  width: 288px;
  height: 128px;
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  border-radius: 16px;
  filter: drop-shadow(0px 12px 16px rgba(0, 0, 0, 0.15));

  > img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.dotGroup {
  position: absolute;
  bottom: 8px;
  right: 8px;
  display: flex;

  > div {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: #C8D0D2;
    margin: 0 4px;
    cursor: pointer;
  }

  > .active {
    background: #FFFFFF;
  }
}

.carousel-prev-enter-active,
.carousel-prev-leave-active,
.carousel-next-enter-active,
.carousel-next-leave-active {
  transition: transform 1s ease;
}

.carousel-next-enter,
.carousel-prev-leave-to {
  transform: translateX(100%);
}

.carousel-next-leave-to,
.carousel-prev-enter {
  transform: translateX(-100%);
}
